<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Invitation Code</h1>
       <div class="chartWrap">
        </div>
    </section>
    <div class="centerWrap">
      <div class="center">
        <van-cell-group>
          <van-field
            placeholder="Enter Invitation Code"
            left-icon="contact"
            right-icon="question-o" v-model="invitationCode"
          />
        </van-cell-group>
        <div class="getInvitation">
            <a @click="$router.push({path:'map',query:{type:1,data:JSON.stringify(from)}})">Get an Invitation Code</a>
        </div>
        <van-button type="danger" @click="submitSignIn">Sigh In</van-button>
      </div>
      
    </div>
  </div>
</template>

<script>

import {addInvitationCode} from '@/api/user'
export default { 
  name: 'fillInvitation',
  data () {
    return {
      invitationCode:this.$route.query.invitationCode||""
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    submitSignIn(){
      addInvitationCode({invitationCode:this.invitationCode}).then(res=>{
           this.$router.push('/')
      });
    }


  }
}
</script>
<style lang="scss">
  .van-field__left-icon .van-icon, .van-field__right-icon .van-icon{
      color: #1f89d3;
    }
</style>


<style lang="scss" scoped>
    .centerWrap{
      width: 100%;
      padding-top: 10px;
      height: 100%;
      background:rgba(246,246,246,1);
    }
    .center{
      width: 100%;
      background-color: #fff;
      padding: 30px 1% 0 1%;
    }
    .getInvitation{
        color: #1f89d3;
        text-align: right;
        padding-right: 15px;
        font-size: 12px;
    }
    .center .van-button--danger{
        width: 90%;
        margin-left: 5%;
        margin-top: 100px;
    }
    
</style>



